module.exports = {
  urlJoin,
  truthy
}

/**
 * Join a base URI and possibly empty path segments
 */
function urlJoin(...args) {
  return args.join('/')
    .replace(/\/\/+/g, '/')
    .replace(/\/+$/, '')
    .replace(':/', '://')
}

/**
 * Return whether a string value should be interpreted as boolean `true`.
 */
function truthy(str) {
  return str === true || str === 1 || str === 'true' || str === '1'
}



